.contacts {
  .title {
    color:         var(--chernyy);
    margin-bottom: clamp(40px, 3vw, 60px);
  }

  .inner {
    display:               grid;
    grid-template-columns: 1fr 1fr;
    gap:                   20px;

    @media(max-width: 1140px) {
      grid-template-columns: 1fr;
    }
  }

  .map {
    border-radius: 8px;
    overflow:      hidden;
  }

  .contacts__block {
    display:        flex;
    flex-direction: column;
    gap:            20px;
    @media(max-width: 1140px) {
      order: -1;
    }
    @media(max-width: 767px) {
      display: flex;
      flex-direction: column-reverse;
    }
    &-top {
      border-radius: 8px;
      padding:       40px 20px;
      background:    var(--goluboy);

    }

    .subtitle {
      font-family:    var(--second-family);
      font-weight:    400;
      @include r-font(32, 24);
      text-transform: uppercase;
      color:          var(--chernyy);
      margin-bottom:  clamp(25px, 2.5vw, 50px);
    }
  }

  .contacts-form {
    display:        flex;
    flex-direction: column;
    gap:            20px;

    .row {
      display:               grid;
      grid-template-columns: 1fr;
      gap:                   10px;
      @media(min-width: 992px) {
        grid-template-columns: 1fr 1fr;
      }

      input {
        border-radius: 4px;
        padding:       12px 24px;
        background:    var(--belyy);
        outline:       none;
        border:        none;
        font-family:   var(--font-family);
        font-weight:   400;
        font-size:     16px;
        line-height:   130%;
        color:         rgba(35, 33, 31, 0.6);
        transition:    .3s ease-in-out;

        &:focus {
          outline: 1px solid var(--bezhevyy);
        }
      }
    }
  }

  .actions {
    margin-top:            20px;
    display:               grid;
    gap:                   10px;

    @media(min-width: 767px) {
      grid-template-columns: 1fr 1fr;

    }
  }

  &__link {
    display:         flex;
    justify-content: center;
    align-items:     center;
    gap:             10px;
    font-family:     var(--font-family);
    font-weight:     500;
    @include r-font(18, 16);
    color:           var(--chernyy);
    border:          1px solid var(--chernyy);
    border-radius:   4px;
    padding:         12px 20px;
    transition:      background-color .3s ease-in-out, color .3s ease-in-out;

    .icon {
      width:  24px;
      height: 24px;
    }

    @media(any-hover: hover) {
      &:hover {
        background-color: var(--hover-btn);
        color:            var(--belyy);
      }
    }
  }

  &__info {
    display:               grid;
    grid-template-columns: 1fr 1fr;
    gap:                   20px;
    @media(max-width: 767px) {
      grid-template-columns: 1fr;
    }

    .block {
      border-radius:  8px;
      padding:        20px;
      border:         1px solid var(--bezhevyy);
      display:        flex;
      flex-direction: column;
      gap:            20px;

      &--black {
        background: var(--chernyy);
        border:     none;

        .text-d {
          color: var(--belyy)
        }
      }

      .icon {
        width:  24px;
        height: 24px;
      }

      .block-element {
        display:               grid;
        grid-template-columns: 24px 1fr;
        align-items:           flex-start;
        gap:                   14px;
      }
    }
  }
}