.breadcrumbs {
  margin-top: 20px;
  margin-bottom: 40px;
  .list {
    display: flex;
    align-items: center;
    gap: 8px;

    span, a{
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 14px;
      color: var(--chernyy);
    }

    a {
      transition: color .3s ease-in-out;
      @include hover{
        &:hover {
          color: var(--bezhevyy);
        }
      }
    }
  }
}