.callback-agent {
  .wrapper {
    position: relative;
    border-radius: 16px;
    overflow:      hidden;
    background:    url("../../img/black-bg.webp") center / cover no-repeat;
  }

  .left {
    padding:   60px 40px;
    @media(min-width: 1240px) {
      max-width: 820px;

    }
    @media(max-width: 767px) {
      padding: 40px 10px;
    }
  }

  .title {
    margin-bottom: 14px;
  }

  .info {
    border-radius: 8px;
    padding:       14px 24px;
    background:    rgba(243, 245, 255, 0.7);
    margin-bottom: 40px;
    @media(min-width: 767px) {
      margin-bottom: 67px;
    }

    span {
      display:       block;
      margin-bottom: 10px;
    }

    a {
      font-family: var(--second-family);
      font-weight: 700;
      @include r-font(24, 20);
      line-height: 120%;
      color:       var(--chernyy);
      transition:  color .3s ease-in-out;
      @include hover {
        &:hover {
          color: var(--bezhevyy);
        }
      }
    }
  }

  .image {
    position: absolute;
    bottom: 0;
    right: 0;
    max-width: 539px;
    width: 39vw;
    @media(max-width: 1240px) {
      display: none;
    }
    img {
      display: block;
    }
  }
}