.news {
  .title {
    color:         var(--chernyy);
    margin-bottom: 10px;
  }

  .slider {
    &__nav {
      display:         flex;
      justify-content: end;
      gap:             20px;
      margin-bottom:   20px;

      &-btn {
        display:       grid;
        place-content: center;
        cursor:        pointer;
        width:         40px;
        height:        40px;
        border-radius: 50%;
        background:    var(--goluboy);
        box-shadow:    0 1px 3px 1px rgba(0, 0, 0, 0.15);
        transition:    background-color 0.3s ease-in-out;

        svg {
          pointer-events: none;

          * > {
            transition: .3s ease-in-out;
          }
        }

        @include hover {
          &:hover {
            background: var(--bezhevyy);

            svg path {
              fill: var(--belyy);
            }
          }
        }
      }
    }
  }

  &__slide {
    display:        flex;
    flex-direction: column;
    height:         auto;
    border-radius:  8px;
    box-sizing:     border-box;
    position:       relative;
    overflow:       hidden;

    .date {
      font-family:   var(--font-family);
      font-style:    italic;
      font-weight:   400;
      font-size:     14px;
      color:         var(--chernyy);
      margin-bottom: 8px;
    }

    &-link {
      position: absolute;
      z-index:  1;
      inset:    0;
    }

    &-img {
      position:      relative;
      padding:       0 0 75% 0;
      overflow:      hidden;
      border-radius: 8px 8px;
      margin-bottom: 14px;

      img {
        position:      absolute;
        top:           0;
        left:          0;
        width:         100%;
        height:        100%;
        object-fit:    cover;
        border-radius: 8px;
        transition:    scale .3s ease;
      }
    }

    &-title {
      font-family:   var(--second-family);
      font-weight:   700;
      @include r-font(20, 18);
      line-height:   120%;
      color:         var(--chernyy);
      margin-bottom: 14px;
      transition:    color .3s ease;
    }

    @include hover {
      &:hover {
        .news__slide-title {
          color: var(--hover-btn);
        }

        .news__slide-img {
          img {
            scale: 1.05;
          }
        }
      }
    }
  }

  .more-link {
    display:         flex;
    justify-content: center;
    align-items:     center;
    gap:             10px;
    max-width:       250px;
    margin:          40px auto 0 auto;
  }
}

