.banner {
  margin-top: 20px;
  height:     450px;
  @media(max-width: 767px) {
    height: auto;
  }

  .container {
    height: 100%;
  }

  .wrapper {
    position:      relative;
    padding:       60px;
    border-radius: 16px;
    overflow:      hidden;
    height:        100%;
    @media(max-width: 767px) {
      padding: 40px 10px;
    }

    &:before {
      content:    '';
      position:   absolute;
      inset:      0;
      z-index:    0;
      background: rgba(0, 0, 0, .6);
    }
  }

  &__img {
    width:    100%;
    height:   100%;
    position: absolute;
    top:      0;
    left:     0;

    img {
      position: absolute;
      inset:    0;
      z-index:  -1;
      width: 100%;
      height: 100%;
    }
  }

  .content {
    position:       relative;
    z-index:        1;
    display:        flex;
    flex-direction: column;
    max-width:      590px;
  }

  .title {
    margin-bottom: 10px;
  }

  &__price {
    border-radius:  8px;
    padding:        8px 28px;
    font-family:    var(--second-family);
    font-weight:    400;
    @include r-font(32, 24);
    text-transform: uppercase;
    color:          var(--chernyy);
    background:     rgba(243, 245, 255, 0.8);
    border-radius:  8px;
    max-width:      fit-content;
    margin-bottom:  clamp(20px, 2.5vw, 40px);
    @media(max-width: 767px) {
      max-width:  100%;
      text-align: center;
    }
  }

  &__time {
    color:         var(--belyy);
    margin-bottom: 10px;
    border:        1px solid var(--goluboy);
    border-radius: 8px;
    padding:       8px 28px;
    text-align:    center;
    max-width:     530px;
  }

  &__desc {
    color:         var(--belyy);
    margin-bottom: clamp(20px, 2.5vw, 40px);
    @media(max-width: 767px) {
      margin-bottom: 100px;
    }
  }

  &__actions {
    display:   flex;
    flex-wrap: wrap;
    gap:       10px;
    max-width: 530px;

    button {
      flex: 1 1 250px;
    }

    .btn--border {
      border-color: var(--belyy);
      color:        var(--belyy);
    }
  }
}