.header {
  padding-top: 10px;
  position:    relative;
  z-index:     9999;

  &__wrapper {
    display:     flex;
    align-items: center;
  }

  .logo {
    display:      flex;
    align-items:  center;
    gap:          10px;
    margin-right: 10px;

    span {
      font-family: var(--font-family);
      font-weight: 400;
      font-size:   16px;
      line-height: 130%;
      color:       var(--chernyy);
      max-width:   164px;
      @media(max-width: 640px) {
        display: none;
      }
    }
  }

  &__year {
    font-family:   var(--second-family);
    font-weight:   700;
    @include r-font(20, 16);
    line-height:   120%;
    color:         var(--chernyy);
    border-radius: 4px;
    padding:       4px 16px;
    background:    var(--goluboy);
    @media(max-width: 1260px) {
      display: none;
    }
  }

  &__actions {
    margin-left: auto;
    display:     flex;
    align-items: center;
    gap:         20px;
    @media(max-width: 1140px) {
      display: none;
    }

    .socials {
      display:     flex;
      align-items: center;
      gap:         10px;
    }
  }

  .header__block {
    display:        flex;
    flex-direction: column;
    gap:            3px;

    span {
      font-family: var(--font-family);
      font-weight: 400;
      font-size:   12px;
      color:       rgba(35, 33, 31, 0.6);
    }
  }

  .header__phone {
    font-family: var(--second-family);
    font-weight: 700;
    font-size:   20px;
    line-height: 120%;
    color:       var(--chernyy);
    transition:  color 0.3s ease-in-out;
    @include hover {
      &:hover {
        color: var(--bezhevyy);
      }
    }
  }

  &__buttons {
    display:        flex;
    flex-direction: column;
    gap:            8px;
  }

  &__bottom {
    backdrop-filter: blur(14px);
    background:      rgba(35, 33, 31, 1);
    padding-block:   10px;
    margin-top:      10px;
    @media(max-width: 1140px) {
      display: none;
    }
  }

  &__menu {
    .list {
      display:         flex;
      gap:             20px;
      justify-content: space-between;
    }

    .link {
      font-family: var(--font-family);
      font-weight: 500;
      color:       var(--belyy);
      @include r-font(18, 16);
      transition:  color 0.3s ease-in-out;
      @include hover {
        &:hover {
          color: var(--bezhevyy);
        }
      }
    }
  }

  .actions-mobile {
    display: none;
    @media(max-width: 1140px) {
      display:     flex;
      align-items: center;
      gap:         10px;
      margin-left: auto;
    }

    .actions-mobile-phone {
      width:         50px;
      height:        50px;
      background:    var(--goluboy);
      display:       grid;
      place-items:   center;
      border-radius: 4px;
    }
  }
}

.sublist-menu {
  position:   absolute;
  width:      100%;
  padding:    40px 0 40px 0;
  background: var(--belyy);
  left:       0;
  top:        45px;
  opacity:    0;
  visibility: hidden;
  transition: 0.1s ease-in-out;

  .sublist-menu-block-wrapper {
    display:               grid;
    grid-template-columns: 60px 1fr;
    align-items:           flex-start;
    gap:                   14px;

    .menu-icon {
      width:         60px;
      height:        60px;
      background:    var(--chernyy);
      border-radius: 4px;
      display:       grid;
      place-items:   center;
    }
  }

  .sublist-menu-nav {
    margin-bottom: 24px;

    .subtitle {
      margin-bottom: 14px;
    }
  }

  .submenu-list {
    display:        flex;
    flex-direction: column;
    gap:            8px;

    a {
      font-family: var(--font-family);
      font-weight: 400;
      font-size:   16px;
      line-height: 130%;
      color:       var(--chernyy);
      transition:  color 0.3s ease-in-out;
      @include hover {
        &:hover {
          color: var(--bezhevyy);
        }
      }
    }
  }

  .sublist-menu-inner {
    display:               grid;
    grid-template-columns: repeat(auto-fill, minmax(330px, 1fr));
    gap:                   20px;
  }
}

.item-sublist .link {
  position: relative;

  svg {
    transition: 0.1s ease-in-out;
  }

  &:before {
    content:  "";
    position: absolute;
    bottom:   -15px;
    left:     0;
    width:    100%;
    height:   40px;
  }
}

.item-sublist:has(.sublist-menu):hover {
  .sublist-menu {
    opacity:    1;
    visibility: visible;
  }

  a {
    svg {
      rotate: 180deg;
    }
  }
}

.burger {
  @include burger;
}

.mobile-menu {
  position:       absolute;
  width:          100%;
  height:         100vh;
  left:           -100%;
  padding:        20px 0;
  background:     var(--goluboy);
  padding-bottom: 300px;
  overflow:       auto;
  opacity:        0;
  visibility:     hidden;
  transition:     .1s ease-in-out;

  &.nav--active {
    left:       0;
    opacity:    1;
    visibility: visible;
  }

  .list {
    display:        flex;
    flex-direction: column;
    gap:            10px;

    .item {
      border-radius: 4px;
      background:    var(--belyy);
      overflow:      hidden;

      & > a {
        font-family:     var(--font-family);
        font-weight:     500;
        font-size:       18px;
        color:           var(--chernyy);
        display:         flex;
        align-items:     center;
        justify-content: space-between;
        gap:             10px;
        padding:         8px;

        &._active {
          background: linear-gradient(227deg, #f1b378 0%, #b97637 100%);
          color:      var(--belyy);

          svg {
            rotate: 180deg;
          }

          svg path {
            fill: var(--belyy);
          }
        }
      }
    }

    .sub-item:has(.submenu-mob) {
      a {
        pointer-events: none;
      }
    }

    .submenu-mob {
      padding-top:    10px;
      padding-inline: 8px;
      padding-bottom: 14px;
      display:        none;

      &.showed {
        display: block;
      }

      .submenu-mob-list {
        display:        flex;
        flex-direction: column;
        gap:            14px;
      }

      a {
        font-family: var(--font-family);
        font-weight: 400;
        font-size:   16px;
        line-height: 130%;
        color:       var(--chernyy);
      }
    }
  }

  .mobile-menu-actions {
    margin-top:     40px;
    display:        flex;
    flex-direction: column;
    gap:            20px;

    .socials {
      display: flex;
      gap:     10px;
    }

    .phones {
      display:               grid;
      grid-template-columns: 1fr 1fr;
      gap:                   20px;

      .phones-block {
        display:        flex;
        flex-direction: column;
        gap:            4px;

        span {
          font-family: var(--font-family);
          font-weight: 400;
          font-size:   12px;
          color:       rgba(35, 33, 31, 0.6);
        }

        a {
          font-family: var(--second-family);
          font-weight: 700;
          font-size:   18px;
          line-height: 120%;
          color:       var(--chernyy);
        }
      }
    }

    .buttons {
      display:        flex;
      flex-direction: column;
      gap:            10px;
    }
  }
}