.agenstvo {
  .title {
    color:         var(--chernyy);
    margin-bottom: clamp(40px, 3vw, 60px);
  }

  .wrapper {
    display:               grid;
    grid-template-columns: 1fr;
    gap:                   clamp(20px, 2.5vw, 40px);
    @media(min-width: 992px) {
      grid-template-columns: 1fr 1fr;
    }
  }

  .left {
    display:        flex;
    flex-direction: column;
    gap:            20px;

    .text-d {
      margin: 0;
    }

    button {
      max-width: fit-content;
      margin-top: auto;
    }

    .list {
      display:        flex;
      flex-direction: column;
      gap:            14px;

      li {
        position:     relative;
        padding-left: 14px;

        &:before {
          content:       '';
          position:      absolute;
          top:           5px;
          left:          0;
          width:         8px;
          height:        8px;
          border-radius: 50%;
          background:    var(--bezhevyy);
        }
      }
    }
  }

  .right {
    img {
      border-radius: 16px;
      width: 100%;
      height: 100%;
    }
  }
}