.garant {

  .garant-title {
    @include r-font(64, 40);
    color:         var(--chernyy);
    margin-bottom: clamp(40px, 3vw, 60px);
  }


  .text-d {
    margin-bottom: 24px;
  }

  .text-d-lg {
    margin-bottom: 24px;
  }

  .logos {
    display:   flex;
    gap:       10px;
    flex-wrap: wrap;
  }
}

.trust {
  .title {
    color:         var(--chernyy);
    margin-bottom: clamp(40px, 3vw, 60px);
  }

  .items {
    display: grid;
    gap:     10px;
    @media(min-width: 767px) {
      grid-template-columns: 1fr 1fr;
    }

    .item {
      border-radius: 8px;
      padding:       14px 20px;
      background:    var(--seryy);

      .item-top {
        display:       flex;
        align-items:   center;
        gap:           16px;
        margin-bottom: 14px;
      }

      .item-icon {
        flex-shrink: 0;
        @media(max-width: 767px) {
          width: 48px;
        }
      }
    }
  }
}

.saved {
  .wrapper {
    border-radius: 8px;
    padding:       20px 40px;
    background:    var(--goluboy);
    @media(max-width: 767px) {
      padding: 20px 10px;
    }

    .text {
      font-family:   var(--second-family);
      font-weight:   700;
      font-size:     20px;
      line-height:   120%;
      color:         var(--chernyy);
      margin-bottom: 14px;
      margin-top:    24px;
    }
  }
}

.garant-content {
  .title {
    color:         var(--chernyy);
    margin-bottom: 40px;
  }

  .wrapper {
    display:               grid;
    grid-template-columns: 1fr 470px;
    gap:                   clamp(20px, 2.5vw, 40px);
    @media(max-width: 992px) {
      grid-template-columns: 1fr;
    }
    .right {
      img {
        border-radius: 16px;
      }
    }
  }
}