.information {
  .inner {
    display:               grid;
    grid-template-columns: 405px 1fr;
    gap:                   20px;
    align-items:           flex-start;

    @media(max-width: 1140px) {
      grid-template-columns: 1fr;
    }
  }

  &__nav {
    display:        flex;
    flex-direction: column;
    gap:            6px;
  }

  &__btn {
    display:               grid;
    grid-template-columns: 60px 1fr;
    align-items:           center;
    gap:                   20px;
    font-family:           var(--second-family);
    font-weight:           700;
    @include r-font(20, 18);
    line-height:           120%;
    color:                 var(--belyy);
    border-radius:         8px;
    padding:               14px 20px;
    cursor:                pointer;
    background:            var(--seryy);
    color:                 var(--chernyy);
    text-align:            left;
    transition:            background-color .3s ease-in-out;

    &._active {
      background-color: var(--bezhevyy);
      color:            var(--belyy);
    }

    @include hover {
      &:hover {
        background-color: var(--bezhevyy);
        color:            var(--belyy);
      }
    }
  }

  &__tabs {
  }

  &__tab {
    border-radius: 8px;
    padding:       40px;
    box-shadow:    0 1px 3px 1px rgba(0, 0, 0, 0.15);
    background:    var(--belyy);
    display:       none;
    @media(max-width: 767px) {
      padding: 20px 10px;
    }
    &._active {
      display: block;
    }

    &-title {
      font-family:   var(--second-family);
      font-weight:   700;
      @include r-font(24, 20);
      line-height:   120%;
      color:         var(--chernyy);
      margin-bottom: 24px;
    }

    .list {
      display:        flex;
      flex-direction: column;
      gap:            24px;
      margin-bottom:  24px;

      li {
        border-bottom: 1px solid rgba(35, 33, 31, 0.2);
        padding:       0px 0px 4px 0px;
      }

      .sublist {
        display:        flex;
        flex-direction: column;
        gap:            8px;

        .sublist-item {
          padding:      0;
          border:       none;
          padding-left: 14px;

          &:before {
            background:    none;
            top:           50%;
            left:          0;
            transform:     translateY(-50%);
            width:         8px;
            height:        8px;
            border-radius: 50%;
            background:    var(--bezhevyy);
          }
        }
      }
    }

    .list-number {
      display:        flex;
      flex-direction: column;
      gap:            24px;
      margin-bottom:  24px;

      li {
        display:       flex;
        align-items:   center;
        gap:           8px;
        border-bottom: 1px solid rgba(35, 33, 31, 0.2);
        padding:       0px 0px 4px 0px;

        .number {
          font-family:             var(--second-family);
          font-weight:             400;
          font-size:               32px;
          text-transform:          uppercase;
          background:              linear-gradient(226deg, #f1b378 0%, #b97637 100%);
          background-clip:         text;
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
        }
      }
    }

    .list-checked {
      b {
        display:       block;
        font-family:   var(--second-family);
        font-weight:   700;
        font-size:     20px;
        line-height:   120%;
        color:         var(--chernyy);
        margin-bottom: 8px;
      }

      li {
        position:     relative;
        padding-left: 30px;

        &:before {
          content:    '';
          position:   absolute;
          top:        -2px;
          left:       0;
          width:      24px;
          height:     24px;
          background: url('../../img/icons/gold-checked.svg') no-repeat;
        }
      }
    }

    .subtitle {
      font-family:   var(--second-family);
      font-weight:   700;
      font-size:     clamp(18px, 1.5vw, 20px);
      line-height:   120%;
      color:         var(--chernyy);
      margin-bottom: 14px;
    }
  }

  &__link {
    display:         flex;
    width:           100%;
    align-items:     center;
    justify-content: space-between;
    gap:             20px;
    font-family:     var(--font-family);
    font-weight:     500;
    font-size:       clamp(16px, 1.5vw, 18px);
    color:           var(--chernyy);
    border-radius:   4px;
    padding:         12px 48px;
    background:      var(--goluboy);
    transition:      background .3s ease-in-out, color .3s ease-in-out;
    @include hover {
      &:hover {
        color:      var(--belyy);
        background: var(--hover-btn);

        svg path {
          fill: var(--belyy);
        }
      }
    }
  }

  &__text {
    display:       block;
    border-radius: 8px;
    padding:       14px 20px;
    font-family:   var(--font-family);
    font-weight:   400;
    font-size:     clamp(14px, 1.5vw, 16px);
    line-height:   130%;
    color:         var(--chernyy);
    background:    var(--seryy);

    b {
      font-family:   var(--second-family);
      font-weight:   700;
      font-size:     20px;
      line-height:   120%;
      color:         var(--chernyy);
      display:       block;
      margin-bottom: 14px;
    }
  }
}