.about-section {
  .title--big {
    @include r-font(64, 40);
    margin-bottom: clamp(40px, 3vw, 60px);
    color: var(--chernyy);
  }

  .wrapper {
    display: grid;
    gap: clamp(20px, 2.5vw, 40px);
    @media (min-width: 1200px) {
      grid-template-columns: 1fr 1fr;
    }
    .title {
      color: var(--chernyy);
      margin-bottom: clamp(40px, 3vw, 60px);
    }
  }

  .left {
    .text-d {
      margin-bottom: 20px;
    }
  }

  .about__image {
    border-radius: 16px;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      border-radius: 16px;
    }
  }

  .about__contacts {
    border-radius: 8px;
    padding: 20px;
    background: var(--goluboy);
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin-top: 10px;
    a {
      font-family: var(--second-family);
      font-weight: 700;
      font-size: 20px;
      line-height: 120%;
      color: var(--chernyy);
      transition: color 0.3s ease-in-out;
      @include hover {
        &:hover {
          color: var(--bezhevyy);
        }
      }
    }
  }
}

.reached {
  .title {
    color: var(--chernyy);
    margin-bottom: 40px;
  }

  &__wrapper {
    border-radius: 16px;
    padding: 40px;
    background: var(--seryy);
  }
  &__items {
    display: grid;
    gap: 20px;
    @media (min-width: 992px) {
      grid-template-columns: 1fr 1fr;
    }
    .item {
      border-radius: 8px;
      padding: 20px;
      background: var(--belyy);
      display: flex;
      align-items: center;
      gap: 14px;

      .logo {
        flex-shrink: 0;
        max-width: 235px;
      }
    }
  }
}

.gallery-about {
  .title {
    color: var(--chernyy);
    margin-bottom: clamp(40px, 3vw, 60px);
  }

  .gallery-about-slider {
    .swiper-slide {
      box-sizing: border-box;

      a {
        display: block;
        border-radius: 16px;
        overflow: hidden;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          border-radius: 16px;
        }
      }
    }

    .slider__nav {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: end;
      -ms-flex-pack: end;
      justify-content: end;
      gap: 20px;
      margin-bottom: 20px;

      &-btn {
        display: grid;
        place-content: center;
        cursor: pointer;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background: var(--goluboy);
        -webkit-box-shadow: 0 1px 3px 1px rgba(0, 0, 0, 0.15);
        box-shadow: 0 1px 3px 1px rgba(0, 0, 0, 0.15);
        -webkit-transition: background-color 0.3s ease-in-out;
        transition: background-color 0.3s ease-in-out;
        @include hover {
          &:hover {
            background: var(--bezhevyy);

            svg path {
              fill: var(--belyy);
            }
          }
        }
      }
    }
  }
}

