.footer {
  background: var(--chernyy);
  padding:    40px 0 20px 0;

  &__btn {
    @media(max-width: 767px) {
      max-width: 100%;
      width:     100%;
    }
  }

  &__row {
    border-bottom:  1px solid rgba(255, 255, 255, 0.4);
    padding-bottom: 22px;
    margin-bottom:  40px;

    .inner {
      display:         flex;
      align-items:     center;
      justify-content: space-between;
      gap:             20px;
      @media(max-width: 1024px) {
        flex-wrap: wrap;
      }
      @media(max-width: 767px) {
        flex-direction: column;
        align-items:    flex-start;
      }
    }


  }

  .logo {
    display:     flex;
    align-items: center;
    gap:         20px;

    span {
      color: var(--belyy);
    }
  }

  &__contacts {
    display:        flex;
    flex-direction: column;
    gap:            20px;

    .phones-top {
      display: flex;
      gap:     clamp(20px, 3vw, 48px);

      .block {
        display:        flex;
        flex-direction: column;
        gap:            4px;

        span {
          font-family: var(--font-family);
          font-weight: 400;
          font-size:   14px;
          color:       rgba(255, 255, 255, 0.6);
        }

        a {
          font-family: var(--second-family);
          font-weight: 700;
          font-size:   clamp(18px, 1.5vw, 20px);
          line-height: 120%;
          color:       var(--belyy);
          transition:  color 0.2s ease-in-out;
          @include hover {
            &:hover {
              color: var(--bezhevyy);
            }
          }
        }
      }
    }

    .phones-bottom {
      display:     flex;
      align-items: center;
      gap:         24px;
      @media(max-width: 767px) {
        flex-wrap: wrap;
      }

      .phones-bottom-row {
        display:     flex;
        align-items: center;
        gap:         14px;

        a {
          display:     flex;
          align-items: center;
          gap:         8px;
          font-family: var(--font-family);
          font-weight: 400;
          font-size:   16px;
          line-height: 130%;
          color:       var(--belyy);
          transition:  color 0.2s ease-in-out;
          @include hover {
            &:hover {
              color: var(--bezhevyy);
            }
          }

          .icon {
            width:  24px;
            height: 24px;
          }
        }
      }

      .email {
        display:     flex;
        align-items: center;
        gap:         8px;
        font-family: var(--font-family);
        font-weight: 400;
        font-size:   16px;
        line-height: 130%;
        color:       var(--belyy);
        transition:  color 0.2s ease-in-out;
        @include hover {
          &:hover {
            color: var(--bezhevyy);
          }
        }

        .icon {
          width:  24px;
          height: 24px;
        }
      }
    }
  }

  &__actions {
    display:     flex;
    align-items: center;
    gap:         20px;
    @media(max-width: 767px) {
      flex-wrap: wrap;
    }

    .socials {
      display:     flex;
      align-items: center;
      gap:         10px;

      a {
        transition:  opacity .3s ease-in-out;
        will-change: opacity;
        @include hover {
          &:hover {
            opacity: .7;
          }
        }
      }
    }
  }

  .info-block {
    display:        flex;
    flex-direction: column;
    gap:            8px;
    flex:           1 1 auto;

    .text-d {
      color: var(--belyy);
    }

    .info-block-stat {
      margin-top:     12px;
      border:         2px solid var(--bezhevyy);
      border-radius:  8px;
      padding:        14px 20px;
      display:        flex;
      flex-direction: column;
      gap:            14px;
      max-width:      190px;

      .text-d {
        color: rgba(255, 255, 255, 0.6);
      }

      .stat {
        font-family: var(--second-family);
        font-weight: 700;
        font-size:   clamp(18px, 2vw, 24px);
        line-height: 120%;
        color:       var(--belyy);
        margin-top:  4px;
      }
    }
  }

  &__nav {
    display:               grid;
    grid-template-columns: repeat(3, 1fr);
    gap:                   clamp(20px, 3.5vw, 60px);
    max-width:             590px;
    margin-right:          93px;
    @media(max-width: 1024px) {
      margin-right: 0;
    }
    @media(max-width: 767px) {
      grid-template-columns: 1fr;
    }

    .nav-block {
      .nav-block-title {
        font-family:   var(--second-family);
        font-weight:   700;
        @include r-font(24, 20);
        line-height:   120%;
        color:         var(--belyy);
        margin-bottom: 20px;
      }

      .list {
        display:        flex;
        flex-direction: column;
        gap:            8px;

        a {
          font-family: var(--font-family);
          font-weight: 400;
          font-size:   14px;
          line-height: 130%;
          color:       var(--belyy);
          transition:  color 0.2s ease-in-out;
          @include hover {
            &:hover {
              color: var(--bezhevyy);
            }
          }
        }
      }
    }
  }

  &__addresses {
    max-width: 390px;

    &-title {
      font-family:   var(--second-family);
      font-weight:   700;
      @include r-font(24, 20);
      line-height:   120%;
      color:         var(--belyy);
      margin-bottom: 20px;
    }

    .address-block {
      display:               grid;
      grid-template-columns: 24px 1fr;
      align-items:           flex-start;
      gap:                   8px;

      &:not(:last-child) {
        margin-bottom: 20px;
      }

      &.text-d {
        color: var(--belyy);
      }

      .icon {
        width:  24px;
        height: 24px;
      }
    }
  }

  .bottom-links {
    .row {
      display:   flex;
      flex-wrap: wrap;
      gap:       clamp(20px, 2.5vw, 40px);

      .text-d {
        color:      rgba(255, 255, 255, 0.6);
        transition: color 0.2s ease-in-out;
        @include hover {
          &:hover {
            color: var(--bezhevyy);
          }
        }
      }
    }
  }
}