.overlay {
  position:         fixed;
  top:              0;
  left:             0;
  width:            100%;
  height:           100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index:          999;
  opacity:          0;
  visibility:       hidden;
}

.header--fixed {
  position:   fixed;
  top:        0;
  left:       0;
  width:      100%;
  background: var(--belyy)
}


.thanks-block {
  .wrapper {
    border-radius: 8px;
    padding:       40px;
    background:    var(--goluboy);

    @media(max-width: 767px) {
      padding: 20px 10px;
    }
  }

  .title {
    color:         var(--chernyy);
    margin-bottom: 24px;
    text-align:    center;
  }

  .text {
    font-family:   var(--second-family);
    font-weight:   700;
    @include r-font(20px, 18);
    line-height:   120%;
    text-align:    center;
    color:         var(--chernyy);
    margin-bottom: clamp(20px, 2.5vw, 40px);
  }

  .contacts {
    display:         flex;
    justify-content: center;
    align-items:     center;
    gap:             20px;
    flex-wrap:       wrap;
    border-radius:   8px;
    padding:         20px;
    box-shadow:      0 1px 3px 1px rgba(0, 0, 0, 0.15);
    background:      var(--belyy);

    .phone {
      display:         flex;
      align-items:     center;
      flex-direction:  column;
      gap:             4px;
      justify-content: center;


      span {
        font-family: var(--font-family);
        font-weight: 400;
        font-size:   14px;
        color:       rgba(35, 33, 31, 0.6);
      }

      a {
        transition: color .3s ease;
        @include hover {
          &:hover {
            color: var(--bezhevyy);
          }
        }
      }
    }

    .socials {
      display:     flex;
      align-items: center;
      gap:         10px;
    }
  }
}

.modal {
  position:    fixed;
  top:         0;
  left:        0;
  width:       100%;
  height:      100%;
  overflow:    auto;
  display:     grid;
  place-items: center;
  z-index:     999999;
  background:  rgba(0, 0, 0, 0.5);
  transition:  .1s ease;
  opacity:     0;
  visibility:  hidden;
  padding:     10px;

  &.modal--active {
    opacity:    1;
    visibility: visible;
  }

  &__container {
    max-width:     1000px;
    border-radius: 8px;
    padding:       40px;
    background:    var(--goluboy);
    position:      relative;
    width:         100%;

    @media(max-width: 767px) {
      padding: 40px 10px;
    }
  }

  &__close {
    position:    absolute;
    top:         10px;
    right:       10px;
    transition:  opacity .3s ease;
    will-change: opacity;
    cursor:      pointer;
    @include hover {
      &:hover {
        opacity: .5;
      }
    }
  }

  .title {
    color:         var(--chernyy);
    margin-bottom: 10px;
    text-align:    center;
  }

  .text-d {
    text-align:    center;
    margin-bottom: 24px;
  }

  .phone {
    font-family:    var(--second-family);
    font-weight:    400;
    font-size:      clamp(28px, 2.5vw, 40px);
    text-transform: uppercase;
    text-align:     center;
    color:          var(--chernyy);
    display:        block;
    text-align:     center;
    margin-bottom:  24px;
  }

  .messangers {
    border-radius: 8px;
    padding:       20px;
    box-shadow:    0 1px 3px 1px rgba(0, 0, 0, 0.15);
    background:    var(--belyy);

    .text-d-lg {
      text-align:    center;
      margin-bottom: 10px;
    }

    .buttons {
      display: grid;
      gap:     10px;
      @media(min-width: 767px) {
        grid-template-columns: 1fr 1fr;
      }

      .btn--border {
        display:         flex;
        align-items:     center;
        gap:             10px;
        justify-content: center;
        padding:         12px;
      }
    }

    .consult {
      display:         flex;
      flex-direction:  column;
      align-items:     center;
      justify-content: center;
      gap:             4px;
      margin-bottom:   20px;

      span {
        font-family: var(--font-family);
        font-weight: 400;
        font-size:   14px;
        color:       rgba(35, 33, 31, 0.6);
      }

      a {
        font-family: var(--second-family);
        font-weight: 700;
        font-size:   20px;
        line-height: 120%;
        text-align:  center;
        color:       var(--chernyy);
        transition:  color .3s ease;

        @include hover {
          &:hover {
            color: var(--bezhevyy);
          }
        }
      }
    }
  }

  &__content {
    max-width: 710px;
  }

  &__img {
    position: absolute;
    right:    -40px;
    bottom:   0;
    @media(max-width: 1023px) {
      display: none;
    }

    img {
      display: block;
    }
  }

  .modal-form {
    margin-bottom:  20px;
    display:        flex;
    flex-direction: column;
    gap:            20px;

    .modal-form-row {
      display:               grid;
      grid-template-columns: 1fr 1fr;
      align-items:           center;
      gap:                   10px;
      @media(max-width: 767px) {
        grid-template-columns: 1fr;
      }

      input {
        border-radius:      4px;
        padding:            12px 24px;
        background:         var(--belyy);
        outline:            none;
        border:             none;
        font-family:        var(--font-family);
        font-weight:        400;
        font-size:          16px;
        line-height:        130%;
        color:              rgba(35, 33, 31, 0.6);
        -webkit-transition: 0.3s ease-in-out;
        transition:         0.3s ease-in-out;

        &:focus {
          border: 1px solid var(--hover-btn);
        }
      }
    }

    .policy {
      position: relative;

      input {
        opacity:            0;
        -webkit-appearance: none;
        -moz-appearance:    none;
        appearance:         none;
        position:           absolute;
        z-index:            1;
        inset:              0;
        cursor:             pointer;
      }

      span {
        font-family:  var(--font-family);
        font-weight:  400;
        font-size:    14px;
        color:        var(--chernyy);
        position:     relative;
        display:      block;
        padding-left: 28px;

        &:before {
          content:       "";
          position:      absolute;
          left:          0;
          top:           1px;
          border-radius: 2px;
          width:         20px;
          height:        20px;
          background:    linear-gradient(226deg, #f1b378 0%, #b97637 100%);
        }
      }

      input:checked + span:before {
        background: url('../img/icons/gradient-checkbox.svg') no-repeat;
      }
    }
  }
}