.main-catalog {
  .title {
    @include r-font(64, 40);
    margin-bottom: clamp(40px, 3vw, 60px);
    color:         var(--chernyy);
  }

  .catalog-tags {
    display:       flex;
    flex-wrap:     wrap;
    gap:           10px;
    margin-bottom: clamp(40px, 3vw, 60px);
    @media(max-width: 992px) {
      display: none;
    }

    .catalog-tag {
      font-family:   var(--second-family);
      font-weight:   700;
      font-size:     20px;
      line-height:   120%;
      text-align:    center;
      color:         var(--chernyy);
      border-radius: 4px;
      padding:       14px 28px;
      flex:          1 1 auto;
      background:    var(--goluboy);
      transition:    background .3s ease, color .3s ease;
      max-width:     fit-content;

      &._active {
        color:      var(--belyy);
        background: var(--chernyy);
      }

      @include hover {
        &:hover {
          color:      var(--belyy);
          background: var(--chernyy);
        }
      }
    }
  }

  .inner {
    display:               grid;
    grid-template-columns: 325px 1fr;
    gap:                   20px;

    @media(max-width: 992px) {
      grid-template-columns: 1fr;
    }
  }

  .item-filter-btn {
    border:          1px solid var(--chernyy);
    border-radius:   4px;
    padding:         12px 40px;
    font-family:     var(--font-family);
    font-weight:     500;
    font-size:       18px;
    color:           var(--chernyy);
    display:         flex;
    align-items:     center;
    gap:             10px;
    justify-content: center;
    transition:      background .3s ease, color .3s ease;
    max-width:       300px;
    margin-left:     auto;
    @include hover {
      &:hover {
        background: var(--chernyy);
        color:      var(--belyy);

        svg path {
          fill: var(--belyy);
        }
      }
    }

    @media(max-width: 992px) {
      font-size:       0;
      padding:         12px;
      justify-content: center;
      gap:             0;
    }
  }

  .btn--more {
    display:         flex;
    align-items:     center;
    justify-content: center;
    gap:             10px;
    max-width:       300px;
    margin:          40px auto 0 auto;
  }

  .items-buttons {
    display:         flex;
    align-items:     center;
    gap:             10px;
    justify-content: space-between;
    margin-bottom:   20px;

  }

  .btn-showed-filter {
    display: none;
    @media(max-width: 992px) {
      display:         flex;
      align-items:     center;
      justify-content: center;
      gap:             10px;
      flex:            1 1 auto;
    }
  }
}

.products {
  display:               grid;
  grid-template-columns: repeat(4, 1fr);
  gap:                   20px;

  @media(max-width: 1320px) {
    grid-template-columns: repeat(3, 1fr);
  }
  @media(max-width: 1024px) {
    grid-template-columns: repeat(2, 1fr);
    gap:                   10px;
  }


}


.product {
  display:        flex;
  flex-direction: column;
  gap:            10px;
  border-radius:  8px;
  padding:        8px;
  background:     var(--seryy);

  .product-image {
    position:      relative;
    padding:       33%;
    border-radius: 8px;
    overflow:      hidden;

    .images {
      display:  flex;
      overflow: hidden;
    }

    .image {
      position:   absolute;
      width:      100%;
      height:     100%;
      top:        0;
      left:       0;
      object-fit: cover;
      display:    block;
    }

    .hvr__sectors {
      position: absolute;
      top:      0;
      left:     0;
      right:    0;
      bottom:   0;
      display:  flex;
    }

    .hvr__sector {
      flex-grow: 1;
    }

    .hvr__dots {
      display:         flex;
      align-items:     center;
      justify-content: center;
      position:        absolute;
      width:           100%;
      left:            50%;
      transform:       translateX(-50%);
      bottom:          10px;
    }

    .hvr__dot {
      flex:          1 1 28px;
      max-width:     28px;
      height:        4px;
      margin:        10px 2px 0;
      border-radius: 4px;
      background:    #d6dbe0;
    }

    .hvr__dot--active {
      background: linear-gradient(226deg, #f1b378 0%, #b97637 100%);
    }
  }

  .product-name {
    font-family:   var(--second-family);
    font-weight:   700;
    @include r-font(20, 18);
    line-height:   120%;
    color:         var(--chernyy);
    margin-bottom: 20px;
    margin-top:    14px;
  }

  .product-content {
    flex: 1 1 auto;
  }

  .product-list {
    display:        flex;
    flex-direction: column;
    gap:            8px;
    margin-bottom:  20px;

    li {
      font-family: var(--font-family);
      font-weight: 400;
      font-size:   14px;
      color:       var(--chernyy);

      span {
        font-family: var(--font-family);
        font-weight: 600;
        font-size:   14px;
        color:       var(--chernyy);
      }
    }
  }

  .product-bottom {
    display:        flex;
    flex-direction: column;
    gap:            14px;

    .product-btn {
      padding: 12px;

    }
  }
}

.filter-wrapper {
  display:        flex;
  flex-direction: column;
  gap:            10px;

  @media(max-width: 992px) {
    display: none;
  }
}

.filter {
  border-radius:  8px;
  padding:        20px 10px;
  background:     var(--seryy);
  display:        flex;
  flex-direction: column;
  gap:            10px;

  .filter-title {
    font-family:     var(--second-family);
    font-weight:     400;
    font-size:       32px;
    text-transform:  uppercase;
    color:           var(--chernyy);
    display:         flex;
    align-items:     center;
    justify-content: space-between;
    gap:             10px;
    cursor:          pointer;

    svg {
      pointer-events: none;
    }
  }


  .filter-options {
    padding:        8px;
    background:     var(--belyy);
    display:        flex;
    flex-direction: column;
    gap:            20px;
    cursor:         pointer;
  }


  .filter-options-toggler {
    display:     flex;
    align-items: center;
    gap:         14px;

    .img {
      flex-shrink:    0;
      pointer-events: none;
    }

    span {
      font-family:    var(--second-family);
      font-weight:    700;
      font-size:      20px;
      line-height:    120%;
      color:          var(--chernyy);
      flex:           1 1 auto;
      pointer-events: none;

    }

    svg {
      pointer-events: none;

    }
  }

  .options {
    display:        none;
    flex-direction: column;
    gap:            12px;

    &.active {
      display: flex;
    }

    .options-item {
      position: relative;

      input {
        position:   absolute;
        z-index:    1;
        cursor:     pointer;
        appearance: none;
        opacity:    0;
        width:      100%;
        height:     100%;
      }

      label {
        font-family:    var(--font-family);
        font-weight:    400;
        font-size:      16px;
        line-height:    130%;
        color:          var(--chernyy);
        transition:     .3s ease;
        padding-block:  8px;
        padding-inline: 16px;

        display:        block;
      }

      input:checked + label {
        border-radius: 4px;
        background:    var(--goluboy);
      }
    }

  }

  .options-checked {

    label {
      position:     relative;
      display:      block;
      padding-left: 32px !important;

      span {
        font-family: var(--font-family);
        font-weight: 400;
        font-size:   16px;
        line-height: 130%;
        color:       rgba(35, 33, 31, 0.4);
      }

      &:before {
        content:       '';
        position:      absolute;
        width:         24px;
        height:        24px;
        border-radius: 2px;
        top:           50%;
        transform:     translateY(-50%);
        left:          0;
        background:    linear-gradient(226deg, #f1b378 0%, #b97637 100%);
      }
    }

    input:checked + label:after {
      content:         "";
      position:        absolute;
      top:             13px;
      left:            6px;
      width:           15px;
      height:          10px;
      background:      url(../../img/icons/checked-white.svg) no-repeat;
      background-size: contain;
    }
  }

  .price-fields {
    display:               grid;
    grid-template-columns: repeat(2, 1fr);
    gap:                   10px;
    margin-bottom:         14px;

    input {
      border:        1px solid var(--bezhevyy);
      border-radius: 8px;
      padding:       8px 16px;
      background:    var(--belyy);
      width:         100%;
    }
  }

  .noUi-horizontal {
  }

  .noUi-horizontal {
    height: 4px;
  }

  .noUi-connect {
    background: var(--bezhevyy);
  }

  .noUi-horizontal .noUi-handle {
    height:        20px;
    width:         20px;
    border-radius: 50%;
    top:           -10px;
    border:        2px solid var(--bezhevyy);

    &:before {
      display: none;
    }

    &:after {
      display: none;
    }
  }

  .filter-reset-btn {
    display:    block;
    text-align: center;

    margin-top: 20px;
  }
}


.products-section {
  .title {
    color:         var(--chernyy);
    margin-bottom: 20px;
  }

  .slider__nav {
    display:         flex;
    justify-content: end;
    gap:             20px;
    margin-bottom:   20px;

    &-btn {
      display:       grid;
      place-content: center;
      cursor:        pointer;
      width:         40px;
      height:        40px;
      border-radius: 50%;
      background:    var(--goluboy);
      box-shadow:    0 1px 3px 1px rgba(0, 0, 0, 0.15);
      transition:    background-color 0.3s ease-in-out;

      svg {
        pointer-events: none;

        * > {
          transition: .3s ease-in-out;
        }
      }

      @include hover {
        &:hover {
          background: var(--bezhevyy);

          svg path {
            fill: var(--belyy);
          }
        }
      }
    }
  }
}

.examples {
  .title {
    color:         var(--chernyy);
    margin-bottom: clamp(40px, 3vw, 60px);
  }

  &__gallery {
    display:   flex;
    flex-wrap: wrap;
    gap:       20px;

    a {
      flex:          1 1 auto;
      overflow:      hidden;
      border-radius: 16px;

      img {
        width:         100%;
        height:        100%;
        display:       block;
        border-radius: 16px;
        transition:    all .3s ease;
      }

      @include hover {
        &:hover {
          img {
            scale: 1.05;
          }
        }
      }
    }
  }

  &__btn {
    max-width:       280px;
    margin:          40px auto 0 auto;
    display:         flex;
    align-items:     center;
    justify-content: center;
    gap:             10px;
  }
}