.price-list {
  .title {
    color: var(--chernyy);
    margin-bottom: clamp(40px, 3vw, 60px);
  }

  &__items {
    display: grid;
    gap: 10px;

    .item {
      border-radius: 8px;
      padding: 14px 40px;
      background: var(--goluboy);
      display: flex;
      align-items: center;
      gap: 15px;
      justify-content: space-between;
      flex-wrap: wrap;
      span {
        font-family: var(--second-family);
        font-weight: 700;
        @include r-font(20px, 18);
        line-height: 120%;
        color: var(--chernyy);
      }
    }
  }
}

.price-content {
  .title {
    color: var(--chernyy);
    margin-bottom: clamp(40px, 3vw, 60px);
  }

  .price-content-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: clamp(20px, 2.5vw, 40px);
  }

  .text-d {
    margin-bottom: 20px;
  }

  .excerpt {
    border-radius: 8px;
    padding: 20px;
    background: var(--goluboy);
    margin-top: 30px;
    a {
      transition: color 0.3s ease-in-out;
      @include hover {
        &:hover {
          color: var(--bezhevyy);
        }
      }
    }
  }

  .right {
    img {
      border-radius: 16px;
      height: 100%;
      width: 100%;
    }
  }
}
