.review {
  .title {
    color:         var(--chernyy);
    margin-bottom: 10px;
  }

  .slider {
    &__nav {
      display:         flex;
      justify-content: end;
      gap:             20px;
      margin-bottom: 20px;
      &-btn {
        display:       grid;
        place-content: center;
        cursor:        pointer;
        width:         40px;
        height:        40px;
        border-radius: 50%;
        background:    var(--goluboy);
        box-shadow:    0 1px 3px 1px rgba(0, 0, 0, 0.15);
        transition:    background-color 0.3s ease-in-out;
        svg {
          pointer-events: none;

          * > {
            transition: .3s ease-in-out;
          }
        }

        @include hover {
          &:hover {
            background: var(--bezhevyy);

            svg path {
              fill: var(--belyy);
            }
          }
        }
      }
    }
  }

  &__slide {
    display: flex;
    flex-direction: column;
    height: auto;
    gap: 20px;
    border-radius: 8px;
    padding: 20px;
    background: var(--seryy);
    box-sizing: border-box;
    &-top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 10px;
      flex-wrap: wrap;

      .name {
        font-family: var(--font-family);
        font-style: italic;
        font-weight: 400;
        font-size: 14px;
        color: var(--chernyy);
      }

      .date {
        font-family: var(--font-family);
        font-style: italic;
        font-weight: 400;
        font-size: 14px;
        color: var(--chernyy);
      }
    }
  }
}