.buro {
  .title {
    color:         var(--chernyy);
    margin-bottom: clamp(40px, 3vw, 60px);
  }

  .inner {
    display:               grid;
    grid-template-columns: 1fr 0.5fr;
    gap:                   20px;
    @media(max-width: 1140px) {
      grid-template-columns: 1fr;
    }
  }

  &__items {
    display:               grid;
    grid-template-columns: 1fr 1fr;
    gap:                   10px;
    @media(max-width: 767px) {
      grid-template-columns: 1fr;
    }
  }

  .item {
    border-radius: 8px;
    padding: 14px 20px;
    background: var(--seryy);
    display: grid;
    grid-template-columns: 48px 1fr;
    gap: 20px;

    .item-content {
      display: grid;
      gap: 8px;

      .subtitle {
        font-family: var(--second-family);
        font-weight: 700;
        @include r-font(24,18);
        line-height: 120%;
        color: var(--chernyy);
      }
    }
  }

  &__block {
    border-radius: 16px;
    padding: 20px;
    background: var(--goluboy);

    .block-title {
      font-family: var(--second-family);
      font-weight: 700;
      @include r-font(24,20);
      line-height: 120%;
      color: var(--chernyy);
      margin-bottom: 14px;
    }

    .text-d {
      margin-bottom: 40px;
    }
  }

  &__btn {
    width: 100%;
  }
}