}

.rent {
  .title {
    color:         var(--chernyy);
    margin-bottom: clamp(40px, 3vw, 60px);
  }

  &__items {
    display: grid;
    gap:     10px;
    @media(min-width: 767px) {
      grid-template-columns: 1fr 1fr;
    }
  }

  .item {
    border-radius:         8px;
    padding:               14px 20px;
    background:            var(--goluboy);
    display:               grid;
    grid-template-columns: 48px 1fr;
    gap:                   20px;

    .content {
      display: grid;
      gap:     8px;
    }
  }

  &__gallery {
    margin-top:            40px;
    display:               grid;
    grid-template-columns: 1fr 1fr;
    gap:                   20px;
    @media(max-width: 767px) {
      grid-template-columns: 1fr;
    }
    .grid-image {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 20px;
    }

    .image {
      display: block;
      height: 100%;
      border-radius: 16px;

      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
        border-radius: 16px;
        transition: scale .3s ease;
      }
      @include hover {
        &:hover {
          img {
            scale: 1.05;
          }
        }
      }
    }
  }
}