.blog {
  .title {
    color:         var(--chernyy);
    margin-bottom: clamp(40px, 3vw, 60px);
  }

  &__items {
    display:               grid;
    grid-template-columns: repeat(4, 1fr);
    gap:                   20px;
    @media(max-width: 1440px) {
      grid-template-columns: repeat(3, 1fr);
    }
    @media(max-width: 1024px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media(max-width: 767px) {
      grid-template-columns: 1fr;
    }
  }

  .item {
    display:        flex;
    flex-direction: column;
    gap:            14px;
    position:       relative;

    .item-link {
      position: absolute;
      z-index:  1;
      inset:    0;
    }

    .item-image {
      position:      relative;
      padding:       0 0 75% 0;
      overflow:      hidden;
      border-radius: 8px 8px;
      margin-bottom: 14px;

      img {
        position:           absolute;
        top:                0;
        left:               0;
        width:              100%;
        height:             100%;
        -o-object-fit:      cover;
        object-fit:         cover;
        border-radius:      8px;
        -webkit-transition: scale 0.3s ease;
        transition:         scale 0.3s ease;
      }
    }

    .text-d {
      margin: 0;
    }

    .date {
      font-family: var(--font-family);
      font-style:  italic;
      font-weight: 400;
      font-size:   14px;
      color:       var(--chernyy);
    }

    .item-title {
      font-family:        var(--second-family);
      font-weight:        700;
      line-height:        120%;
      color:              var(--chernyy);
      -webkit-transition: color 0.3s ease;
      transition:         color 0.3s ease;
      @include r-font(20, 18);
    }

    @include hover {
      &:hover {
        img {
          scale: 1.05;
        }

        .item-title {
          color: var(--bezhevyy);
        }
      }
    }
  }

  &__more {
    display:         flex;
    justify-content: center;
    align-items:     center;
    gap:             10px;
    max-width:       280px;
    margin:          0 auto;
    margin-top:      40px;
  }
}


.single-blog-image {
  margin-top: 40px;

  .wrapper {
    border-radius: 8px;
    overflow:      hidden;

    img {
      border-radius: 8px;
      width:         100%;
    }
  }
}

.single-blog-content {
  .wrapper {
    max-width: 1200px;
    margin:    0 auto;
  }

  .title {
    color:         var(--chernyy);
    margin-bottom: clamp(40px, 3vw, 60px);
  }

  .nav-article {
    border-radius: 8px;
    padding:       20px 40px;
    background:    var(--seryy);
    margin-bottom: 40px;
    @media(max-width: 767px) {
      padding: 20px;
    }

    .nav-article-title {
      font-family:   var(--second-family);
      font-weight:   700;
      @include r-font(24, 20);
      line-height:   120%;
      color:         var(--chernyy);
      margin-bottom: 20px;
    }

    .list {
      display:        flex;
      flex-direction: column;
      gap:            16px;

      li {
        border-bottom: 1px solid rgba(35, 33, 31, 0.2);
        padding:       0px 0px 2px 0px;
      }

      .text-d {
        position:     relative;
        padding-left: 24px;
        transition:   all .3s ease-in-out;

        &:before {
          content:    '';
          position:   absolute;
          top:        50%;
          left:       0;
          transform:  translateY(-50%);
          width:      17px;
          height:     9px;
          background: url('../../img/icons/arrow-long.svg') no-repeat;
        }

        @include hover {
          &:hover {
            margin-left: 10px;
          }
        }
      }
    }
  }

  .content-block {
    p {
      font-family:   var(--font-family);
      font-weight:   400;
      font-size:     16px;
      line-height:   130%;
      color:         var(--chernyy);
      margin:        0;
      margin-bottom: clamp(20px, 2.5vw, 40px);
    }

    h2 {
      font-family:   var(--second-family);
      font-weight:   700;
      @include r-font(24, 20);
      line-height:   120%;
      color:         var(--chernyy);
      margin:        0;
      margin-bottom: 25px;
    }

    ul {
      padding:        0;
      margin:         0 0 40px 0;
      display:        flex;
      flex-direction: column;
      gap:            14px;
      list-style:     none;

      li {
        display:        flex;
        flex-direction: column;
        gap:            4px;
        font-family:    var(--font-family);
        font-weight:    400;
        font-size:      16px;
        line-height:    130%;
        color:          var(--chernyy);
        position:       relative;
        padding-left:   14px;

        b {
          font-family: var(--second-family);
          font-weight: 700;
          font-size:   20px;
          line-height: 120%;
          color:       var(--chernyy);
        }

        &:before {
          content:       '';
          position:      absolute;
          top:           7px;
          left:          0;
          width:         8px;
          height:        8px;
          border-radius: 50%;
          background:    var(--bezhevyy);
        }
      }
    }

    .exceprt {
      border-radius: 8px;
      padding: 20px 40px;
      background: var(--goluboy);
      @media(max-width: 767px) {
        padding: 20px 10px;
      }
    }
  }
}