.form {
  display:        flex;
  flex-direction: column;
  gap:            10px;

  .row {
    display:               grid;
    grid-template-columns: 1fr;
    gap:                   10px;
    @media(min-width: 767px) {
      grid-template-columns: 1fr 1fr;
    }

    input {
      border-radius: 4px;
      padding:       12px 24px;
      background:    var(--belyy);
      outline:       none;
      border:        none;
      font-family:   var(--font-family);
      font-weight:   400;
      font-size:     16px;
      line-height:   130%;
      color:         rgba(35, 33, 31, 0.6);
      transition:    .3s ease-in-out;

      &:focus {
        outline: 1px solid var(--bezhevyy);
      }
    }
  }

  .policy {
    margin-top: 10px;
    position:   relative;

    .checkbox {
      opacity:    0;
      appearance: none;
      position:   absolute;
      z-index:    1;
      inset:      0;
      cursor:     pointer;
    }

    span {
      font-family:  var(--font-family);
      font-weight:  400;
      @include r-font(14, 12);
      color:        var(--belyy);
      display:      block;
      padding-left: 20px;

      &:before {
        content:       "";
        position:      absolute;
        left:          0;
        top:           1px;
        border-radius: 2px;
        width:         15px;
        height:        15px;
        background:    var(--goluboy);
      }

      &:after {
        content:         "";
        position:        absolute;
        left:            3px;
        top:             5px;
        width:           9px;
        height:          9px;
        background:      url('../../img/icons/checkbox.svg') no-repeat;
        background-size: contain;
        opacity:         0;
        visibility:      hidden;
        transition:      .3s ease-in-out;
      }
    }

    .checkbox:checked + span:after {
      opacity:    1;
      visibility: visible;
    }
  }

  .actions {
    margin-top:            20px;
    display:               grid;
    grid-template-columns: 1fr;
    gap:                   10px;
    @media(min-width: 767px) {
      grid-template-columns: 1fr 1fr;
    }

    &__link {
      display:         flex;
      justify-content: center;
      align-items:     center;
      gap:             10px;
      font-family:     var(--font-family);
      font-weight:     500;
      @include r-font(18, 16);
      color:           var(--belyy);
      border:          1px solid var(--belyy);
      border-radius:   4px;
      padding:         12px 20px;
      transition:      background-color .3s ease-in-out, color .3s ease-in-out;

      .icon {
        width:  24px;
        height: 24px;
      }

      @media(any-hover: hover) {
        &:hover {
          background-color: var(--hover-btn);
          color:            var(--belyy);
        }
      }
    }
  }
}