.default-page {
  .title {
    @include r-font(60, 40);
    margin-bottom: clamp(40px, 3vw, 60px);
    color:         var(--chernyy);
  }

  .subtitle {
    margin-bottom: 24px;
  }

  .list {
    margin-bottom: 40px;
  }

  .images {
    display:               grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap:                   10px;
    margin-bottom:         40px;
    @media(max-width: 767px) {
      grid-template-columns: 1fr 1fr;
    }
  }

  .block-text {
    margin-bottom: 40px;

    .text-d:not(:last-child) {
      margin-bottom: 14px;
    }
  }

  .desc {
    margin-bottom: clamp(20px, 2.5vw, 40px);
  }

  .d-list-ol  {
    counter-reset: steps;
    li {
      position: relative;
      padding-left: 35px;
      &:before{
        content: counter(steps) '.';
        counter-increment: steps;
        background: none;
        position: absolute;
        top: 70%;
        left: 0;
        transform: translateY(-50%);
        width: 100%;
        height: 100%;
        font-family: var(--second-family);
        font-weight: 400;
        @include r-font(32,24);
        text-transform: uppercase;
        color: var(--siniy);
      }
      span {
        display: block;
        margin-bottom: 4px;
      }
    }

  }

  strong {
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 20px;
    line-height: 120%;
    color: var(--chernyy);
    margin-bottom: 14px;
    display: block;
  }
}