.section-contacts {
  .title {
    color:         var(--chernyy);
    margin-bottom: clamp(40px, 3vw, 60px);
  }

  .contacts-top {
    display:       flex;
    align-items:   center;
    border-radius: 8px;
    padding:       40px;
    background:    var(--goluboy);
    margin-bottom: 60px;
    gap:           20px;
    flex-wrap:     wrap;

    @media(max-width: 992px) {
      padding: 20px 10px;
    }

    .left {
      flex: 1 1 auto;
    }

    .phones {
      display: flex;
      gap:     20px;

      .phones-block {
        span {
          font-family:   var(--font-family);
          font-weight:   400;
          font-size:     14px;
          color:         #838383;
          display:       block;
          margin-bottom: 4px;
        }

        a {
          font-family: var(--second-family);
          font-weight: 700;
          font-size:   20px;
          line-height: 120%;
          color:       var(--chernyy);
          transition:  color .3s ease;
          @include hover {
            &:hover {
              color: var(--bezhevyy);
            }
          }
        }
      }
    }

    .socials {
      display:      flex;
      gap:          10px;
      margin-right: 40px;
    }

    .mail {
      display:        flex;
      flex-direction: column;
      gap:            4px;

      .mail-text {
        display:     flex;
        align-items: center;
        gap:         3px;
      }

      a {
        transition: color .3s ease-in;
        @include hover {
          &:hover {
            color: var(--bezhevyy);
          }
        }
      }
    }
  }

  .contacts-map {
    display: grid;
    gap:     20px;

    .map {
      iframe {
        border:        none;
        border-radius: 20px;
      }
    }

    @media(min-width: 992px) {
      grid-template-columns: repeat(2, 1fr);
    }

    .contacts-map-content {
      display: grid;
      gap:     20px;

      .block {
        border-radius:  8px;
        padding:        20px;
        display:        flex;
        flex-direction: column;
        gap:            20px;

        .block-el {
          display:     flex;
          align-items: center;
          gap:         14px;

          .icon-24 {
            flex-shrink: 0;
          }
        }
      }

      .block--black {
        background: var(--chernyy);

        .text-d {
          color: var(--belyy);
        }
      }

      .block--border {
        border: 2px solid var(--bezhevyy);
      }
    }
  }

  .contacts-end {
    margin-top:    120px;
    border-radius: 8px;
    padding:       40px;
    background:    var(--seryy);
    @media(max-width: 992px) {
      margin-top: 60px;
      padding:    20px 10px;
    }

    .text-d-lg {
      margin-bottom: 20px;
    }

    .wrapper {
      display:                grid;
      grid-template-columns:  1fr 1fr;
      gap:                    clamp(20px, 2.5vw, 40px);
      @media(max-width: 992px) {
        grid-template-columns: 1fr;
      }

      .list {
        display: grid;
        gap:     14px;

        li {
          display:         flex;
          align-items:     center;
          justify-content: space-between;
          gap:             10px;
          flex-wrap:       wrap;
          @media(max-width: 767px) {
            flex-direction: column;
            align-items:    flex-start;
          }

          span {
            font-family: var(--second-family);
            font-weight: 700;
            @include r-font(20, 18);
            line-height: 120%;
            color:       var(--chernyy);
          }
        }
      }
    }
  }
}