.catalog-section {
  .title {
    color:         var(--chernyy);
    margin-bottom: 20px;
    @include r-font(60, 40);
  }

  .text-d {
    max-width:     710px;
    margin-bottom: clamp(40px, 3vw, 60px);
  }

  .items {
    display:   flex;
    flex-wrap: wrap;
    gap:       20px;

    .item {
      display:       flex;
      align-items:   center;
      gap:           10px;
      border-radius: 8px;
      padding:       14px;
      background:    var(--goluboy);
      position:      relative;
      flex:          1 1 20%;
      @media(max-width: 1540px) {
        flex: 1 1 30%;
      }
      @media(max-width: 1024px) {
        flex: 1 1 auto;
      }
      @media(max-width: 767px) {
        flex: 1 1 100%;
      }
      .item-icon {
        transition: .3s ease-in-out;
        flex-shrink: 0;
      }

      p {
        font-family: var(--second-family);
        font-weight: 700;
        @include r-font(24, 20);
        line-height: 120%;
        color:       var(--chernyy);
      }

      .item-link {
        position: absolute;
        inset:    0;
        z-index:  1;
      }

      @include hover {
        &:hover {
          .item-icon {
            scale:  1.05;
            rotate: 5deg;
          }
        }
      }
    }
  }
}