.error-main {
  background:     url("../../img/bg-404.webp") center / cover no-repeat;
  padding-bottom: 40px;

  .callback-agent {
    margin-bottom: 0;
    transform:     translateY(-300px);

    @media(max-width: 1140px) {
      transform: translateY(0);
    }
  }
}

.error-page {
  padding-top:   80px;
  margin-bottom: 0;
  @media(max-width: 767px) {
    padding-top:   20px;
    margin-bottom: 50px;
  }

  .wrapper {
    display:               grid;
    grid-template-columns: 1fr 1fr;
    gap:                   20px;
    position:              relative;
    @media(max-width: 767px) {
      grid-template-columns: 1fr;
    }
  }

  .left-side {
    transform: translateY(100px);
    max-width: 460px;
    @media(max-width: 767px) {
      transform:   translateY(0);
      max-width:   260px;
      padding-top: 60px;
    }
  }

  .title-error {
    color:         var(--chernyy);
    margin-bottom: 20px;
    @include r-font(64, 40);

  }

  .text-d {
    margin-bottom: 40px;
    max-width:     460px;
  }

  .btn--accent {
    display:         flex;
    align-items:     center;
    justify-content: center;
    gap:             10px;
    @media(max-width: 767px) {
      padding: 12px
    }
  }

  .right-side {
    @media(max-width: 767px) {
      transform: translateY(100px);
      position:  absolute;
      bottom:    0;
      right:     -100px;
      max-width: 220px;
    }
  }
}
